<script>
export default {
	name: "Control",
	props: {
		lf: "",
		catTurboData: Boolean,
	},
	data() {
		return {
			undoDisable: true,
			redoDisable: true,
			graphData: null,
			dataVisible: false,
		};
	},
	mounted() {
		this.$props.lf.on("history:change", ({ data: { undoAble, redoAble } }) => {
			this.$data.undoDisable = !undoAble;
			this.$data.redoDisable = !redoAble;
		});
	},
	methods: {
		$_zoomIn() {
			this.$props.lf.zoom(true);
		},
		$_zoomOut() {
			this.$props.lf.zoom(false);
		},
		$_zoomReset() {
			this.$props.lf.resetZoom();
		},
		$_translateRest() {
			this.$props.lf.resetTranslate();
		},
		$_reset() {
			this.$props.lf.resetZoom();
			this.$props.lf.resetTranslate();
		},
		$_undo() {
			this.$props.lf.undo();
		},
		$_redo() {
			this.$props.lf.redo();
		},
		$_download() {
			this.$props.lf.extension.snapshot.customCssRules = `
    .lf-canvas-overlay {
      background: #f0f4fb;
    }
  `;
			this.$props.lf.getSnapshot();
		},
		$_catTurboData() {
			this.$emit("catTurboData");
		},
		$_showMiniMap() {
			const { lf } = this.$props;
			lf.extension.miniMap.show(lf.graphModel.width - 175, 220);
		},
	},
};
</script>
